import React, {Component, Fragment} from 'react';
import {Route, Redirect, withRouter} from 'react-router-dom';
import './App.scss';
import TopNav from "./components/TopBar/TopBar";
import SubNav from "./components/SubNav/SubNav";
import SideBar from "./components/SideBar/SideBar";
import Content from "./components/Content/Content"
import {connect} from "react-redux";

class App extends Component {
  render() {
    const {appData: {basePath}} = this.props;
    return <Fragment>
      <Route path={`${basePath}`} exact={true} component={() => <Redirect to={`${basePath}buy/live-lanes`} />} />
      <Route path={`${basePath}sleepy-elbakyan`} exact={true} component={() => <Redirect to={`${basePath}buy/live-lanes`} />} />
      <Route path={`${basePath}buy`} exact={true} component={() => <Redirect to={`${basePath}buy/live-lanes`} />} />
      <TopNav />
      <SubNav />
      <main>
        <SideBar />
        <Content />
      </main>
    </Fragment>;
  }
}

export default withRouter(connect(({appData}) => ({appData}))(App));
